.App{
    display: grid;
    grid-template-rows: 1fr 7fr;
    height: inherit;
    background-size: 300% 300%;
    background-image: linear-gradient(
        -45deg, 
        #608396 0%, 
        rgba(146, 93, 85, 0.836) 25%, 
        rgba(116, 117, 88, 0.801) 51%, 
        rgba(121, 71, 89, 0.863) 100%
  );  
  animation: AnimateBG 20s ease infinite;


    .header{
        
        display: grid;
        grid-template-columns: 1fr 2fr;
        box-shadow: 0 30px 60px 0 rgba(173, 151, 134, 0.4);

        .logo{
            position: relative;
            left: 10%;
            top:10%;
        
            img{
                width: 60px;
                height:60px;
                border-radius: 50%;
                padding: 5px 5px;
                &:hover {
                    width: 70px;
                    height:70px;
                    box-shadow:  0 0 20px rgb(205, 206, 199);
                }
                
            }
        }
        nav{
            display:grid;
            grid-template-columns: 1fr 1fr 1fr 1fr;
            grid-gap: 10px;
            text-align: center;
            top: 20%;
            position: relative;
            height: 80%;
            padding-right: 30px;
            font-family: 'Helvetica Neue';
            font-weight: 800;

            a{
                    font-size: 22px;//25
                    color: #a7a7b3;
                    display: block;
                    line-height: 51px;
                    height: 51px;
                    position: relative;
                    text-decoration: none;

                    i{
                        transition: all 0.3s ease-out;
                    }
                    &:hover {
                        color: #d5dcdf;
            
                        svg{
                            opacity: 0;
                        }
            
                        &:after{
                            opacity: 1;
                        }
                    }
                    &:after{
                        content: '' ;
                        font-size: 1.5vw ;
                        letter-spacing: 2px;
                        position: absolute;
                        bottom: 0;
                        display: block;
                        width: 100%;
                        text-align: center;
                        opacity: 0;
                        transition: all 0.3s ease-out;
                    }
                }
                a.home-link{
                    &:after{
                        content: 'HOME';
                    }
                }
            
                    a.about-link{
                        &:after{
                            content: 'ABOUT';
                        }
                    }
            
                    a.contact-link{
                        &:after{
                            content: 'CONTACT';
                        }
                    }
                    a.article-link{
                        &:after{
                            content: 'ARTICLES';
                        }
                    }
                    a.active{
                        svg{
                            color:#312e33;
                        }
                        &:hover {
                            color: #312e33;
                        }
                    }
            
            
            }
            
        }

        @media screen and (min-width: 600px) {
            .header {
              grid-template-columns: 1.3fr 1fr;
              grid-column-gap: 10px;
            }
          }
        
    }
    @keyframes AnimateBG { 
        0%{background-position:0% 50%}
        20%{background-position:50% 50%}
        50%{background-position:100% 50%}
        75%{background-position: 50% 50%}
        100%{background-position:0% 50%}
      }

      @media screen and (max-width: 600px) {
        .App{
            .header{
                .logo{
                    left:20%;
                    top:20%;
                }

            
            nav{
                top: 32%;
                
                a{
                    &:hover {
                        color: #e6e9eb;
                    }
                    &:after{
                        font-size: 2.2vw ;
                    }

                }
            }
        }
        }
      }