.home-page {
  display: block;
  position: relative;
  top: 20%;
  text-align: center;
  width: 50%;
  height:60%;
  left:25%;
  
  
  
  

    h1 {
      color: rgb(233, 219, 219);
      font-size: 6vw;
      line-height: 53px;
      margin: 50px 5px 30px 5px;
      font-weight: 400;
      font-family: 'La Belle Aurore';
      animation: bounce; /* referring directly to the animation's @keyframe declaration */
      animation-duration: 2s;
    }
   
    h2 {
      color: #eae6de;
      margin-bottom: 40px;
      font-weight: 400;
      font-size: 2vw;
      font-family: 'Helvetica Neue';
      font-weight: 900;
      letter-spacing: 3px;
      animation:bounce;
      animation-delay: 3s;
    }
  
    .flat-button {
      color: #daf0e9;
      font-size: 13px;
      font-weight: 400;
      letter-spacing: 4px;
      font-family: sans-serif;
      text-decoration: none;
      padding: 10px 18px;
      border: 2px solid #b3b9c5;
      border-radius:20%;
      margin-top: 35px;
  
      animation: linear 1s 1.8s backwards;
      white-space: nowrap;
  
      &:hover {
        background: #312e33;
        color: rgb(235, 223, 223);
      }
    }

    

}
  
  
  
  @media screen and (max-width: 600px) {
    
    .home-page{
      height:50%;
      position: relative;
      top: 5%;
      width: fit-content;
      left: 20%;
      h2{
        font-size: 3vw;
        font-weight: 100;
        letter-spacing: 0px;
      }

      .flat-button{
        margin-top:25px;
      }
    }
    }
    @keyframes bounceh1{
      0%{ }
      
    }