.article-page{
    font-family: 'La Belle Aurore';
    position: relative;
    color:rgb(236, 204, 195);
    width: 30%;
    height:20%;
    text-align: center;
    top: 20%;
    left: 35%;
    font-size: 2vw;
    word-wrap: 0;
    text-shadow:  0 0 20px rgb(187, 178, 96);
    &:hover{
        font-size: 3vw;
        color: rgb(66, 66, 61);
        
    }
    animation: animate 5s infinite ease-out;
}
@keyframes animate { 
    0%{font-size: 2vw}
    25%{font-size: 2.5vw;color: rgb(216, 211, 202);}
    50%{font-size: 2.7vw;color: rgb(56, 53, 53);}
    100%{font-size: 2vw}
  }