.contact-page{
    color:rgb(248, 246, 242);
    width: 80%;
    position: relative;
    left: 10%;
    display: grid;
    grid-template-rows: 0.6fr 2fr 1fr;
    text-align: center;
    height: 80%;
    top:5%;
    font-family: 'Coolvetica';
    h1{
        position: relative;
        top: 10%;
        font-size: 6vw;
        text-align: center;
        color: rgb(240, 233, 233);
    }

    .contact-form{
        padding-top: 3%;
        display: grid;
        grid-template-rows: 1fr 1fr 1fr 4fr 2fr;
        grid-row-gap:5px;
        width: 30%;
        position: relative;
        left: 35%;
        text-align: center;
        textarea,input{
            border-radius: 10px;
            padding-left: 5%;
            
        }
        
        .flat-button{
            padding:0px;
            width:30%;
            height:80%;
            top:15px;
            position: relative;
            left:35%;
            color: #131112;
            font-weight: 600;
            border-radius: 20px;
            &:hover {
                background: #312e33;
                color: rgb(235, 223, 223);
              }
              
        }

    
        
    }

   
    
    .footer-content{
        width: 60%;
        height: 60%;
        position: relative;
        top: 40%;
        left:20%;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;

        a{
            font-size: 3vw;//25
            color: #efeff8;
            display: block;
            line-height: 51px;
            height: 51px;
            position: relative;
            text-decoration: none;
            .icons-a{
                
                &:hover {
                    font-size: 5vw;
                    color:#312e33;
                }
            }


           
        }
      }
      .message{
            margin-top: 50px;
            background-color: rgb(144, 187, 154);
            font-family: 'Coolvetica';
            font-size: 1.6vw;
            height:50px;
            width:60%;
            position: relative;
            left: 20%;
            text-align: center;
            padding-top: 10px;
            border-radius: 40px;
            box-shadow:  0 0 20px rgb(185, 187, 97);
            color: #353336;
            animation: buttons 2s ease-out 1;
            animation-play-state:paused;
            opacity: 0;
            
      }

}
@media screen and (max-width: 600px) {
    .contact-page{
       
    .contact-form{
        left: 25%;
    }
    .flat-button{
        top: 10%;
        width:25%;
    }
    .footer-content{
        a{
            font-size: 10vw;
        }
    }
}
}
@keyframes buttons{
    0%{transform: translateY(100%);width: 40%;}
    
    50%{transform: translateY(60%);width: 55%;}
   
    100%{transform: translateY(20%);width: 60%;}
}
