.about-page{
    display: block;
    position: relative;
    width: 80%;
    height: 100%;
    left: 10%;
    top: 3%;
    color: #ecdbcc;
    text-align: justify;
    padding: 20px;
    font-family: 'Helvetica Neue';
    font-weight: 100;
    

    h1{
        margin: 10px;
        font-size: 5vw;
        
    }
    p{
        margin:5px;
        font-size: 1.2vw;
        &:hover {
            font-family: 'Coolvetica';
            color: #312e33;
            font-size: 1.5vw;
            font-weight: 500;
        }
    
    }
    .p2{
        font-family: 'Coolvetica';
            color: #f6f2f8;
            font-size: 2vw;
            font-weight: 500;

    }
    
    li{
        list-style: none;
        &:hover {
            font-family: 'Coolvetica';
            color: #312e33;
            font-size: 2vw;
            text-shadow:  0 0 20px rgb(194, 191, 162);
        }
    }
    
}